<template>
  <v-container>
    <div>
      <v-toolbar>
        <v-toolbar-side-icon>
          <v-icon @click="showRanking">menu</v-icon>
        </v-toolbar-side-icon>
        <v-toolbar-title>골드러쉬</v-toolbar-title>
      </v-toolbar>

      <v-container text-xs-center grid-list-md>
        <Done v-model="show" :show.sync="show" :type="result"/>
        <v-layout>
          <v-flex xs12 lg8 offset-lg2>
            <transition name="fade">
              <v-img :src="bg">
                <v-container v-if="localStage === 1 || localStage === 5" fill-height mt-3>
                  <v-layout justify-content="space-evenly">
                    <v-flex xs4 lg2 align-self-end @click="selectDoor(1)">
                      <v-img
                        v-if="selectedDoor === 1"
                        :src="require('@/assets/minimg/d_heart_on.png')"
                      ></v-img>
                      <v-img v-else :src="require('@/assets/minimg/d_heart_off.png')"></v-img>
                    </v-flex>
                    <v-flex xs4 lg2 align-self-end @click="selectDoor(2)">
                      <v-img
                        v-if="selectedDoor === 2"
                        :src="require('@/assets/minimg/d_circle_on.png')"
                      ></v-img>
                      <v-img v-else :src="require('@/assets/minimg/d_circle_off.png')"></v-img>
                    </v-flex>
                    <v-flex xs4 lg2 align-self-end @click="selectDoor(3)">
                      <v-img
                        v-if="selectedDoor === 3"
                        :src="require('@/assets/minimg/d_square_on.png')"
                      ></v-img>
                      <v-img v-else :src="require('@/assets/minimg/d_square_off.png')"></v-img>
                    </v-flex>
                  </v-layout>
                </v-container>
                <v-container v-else-if="localStage === 2" fill-height mt-3>
                  <v-layout>
                    <v-flex align-self-end mb-4>
                      <v-responsive class="swiper-inner">
                        <swiper :options="swiperOption">
                          <swiper-slide v-for="i in 10" :key="i" :class="`slide-${i}`"></swiper-slide>
                        </swiper>
                      </v-responsive>
                    </v-flex>
                  </v-layout>
                </v-container>
                <v-container v-else-if="localStage === 3" fill-height mt-3>
                  <v-layout>
                    <v-flex align-self-end>
                      <v-layout mt-4>
                        <v-flex v-for="(item, i) in items" :key="i">
                          <img
                            width="60"
                            height="60"
                            :src="require(`@/assets/minimg/num${item}.png`)"
                          >
                          <!-- 2초간 보여질 랜덤 힌트 카드 4개 localStage 3으로 변경, 2초후 다시 2로-->
                        </v-flex>
                      </v-layout>
                    </v-flex>
                  </v-layout>
                </v-container>
                <v-container v-else-if="localStage === 4" fill-height mt-3>
                  <v-layout>
                    <v-flex align-self-end mb-3>
                      <h2>축하합니다! 보물을 획득했습니다.</h2>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-img>
            </transition>
          </v-flex>
        </v-layout>

        <v-card>
          <v-card-text class="grey lighten-2 text--secondary text-xs-left">
            <h4>{{ guideMessage }}</h4>
          </v-card-text>
          <v-layout justify-center row>
            <v-flex xs3 md2 ml-3 mt-0 mb-2>
              <v-img
                v-if="localStage === 1 && scanner > 0 && scannerStatus === 1"
                :src="require('@/assets/minimg/scanner_on.png')"
                @click="useScanner"
              ></v-img>
              <v-img
                v-else-if="localStage === 1 && scanner > 0 && scannerStatus === 2"
                :src="require('@/assets/minimg/scanner_sel.png')"
                @click="useScanner"
              ></v-img>
              <v-img v-else :src="require('@/assets/minimg/scanner_off.png')"></v-img>
            </v-flex>
            <v-flex xs3 md2 align-self-center>
              <v-layout justify-start column>
                <h4>황금탐지기</h4>
                <v-flex>
                  <v-avatar :color="scannerColor">
                    <span class="white--text headline">{{ scanner }}</span>
                  </v-avatar>
                </v-flex>
              </v-layout>
            </v-flex>
            <v-flex xs3 md2 ml-3 mt-0 mb-2>
              <v-img
                v-if="localStage === 2 && athena > 0 && athenaStatus === 1"
                :src="require('@/assets/minimg/athena_on.png')"
                @click="useAthena"
              ></v-img>
              <v-img v-else :src="require('@/assets/minimg/athena_off.png')"></v-img>
            </v-flex>
            <v-flex xs3 md2 align-self-center>
              <v-layout justify-end column>
                <h4>아테나</h4>
                <v-flex>
                  <v-avatar :color="athenaColor">
                    <span class="white--text headline">{{ athena }}</span>
                  </v-avatar>
                </v-flex>
              </v-layout>
            </v-flex>
          </v-layout>
        </v-card>
      </v-container>
    </div>

    <Ranking v-model="rank" :rank.sync="rank"/>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Bonus",
  components: {
    Done: () => import("./Done.vue"),
    Ranking: () => import("./Ranking.vue")
  },
  data() {
    return {
      show: false,
      rank: false,
      result: 0, // 0: fail, 1: success (다이어로그 메시지 선택에 필요)
      bg: require("@/assets/minimg/fanfare.gif"), //require("@/assets/minimg/castle.png"),
      selectedDoor: 0,
      localStage: -1, // 1: 방선택, 2: 비밀번호 맞추기, (3: 아테나 사용,) 4:보관 안내, 5: 보물 보관하기, 0: 전환과정에서 머무르는 임시
      scannerStatus: 1, // 1: unused, 2: used, 3: can't use
      athenaStatus: 1, // 1: unused, 2: used, 3: can't use
      scannerColor: "grey",
      athenaColor: "grey",
      swiperOption: {
        effect: "cube",
        grabCursor: true,
        cubeEffect: {
          shadow: true,
          slideShadows: true,
          shadowOffset: 20,
          shadowScale: 0.94
        },
        on: {
          init: function() {
            console.log("initialized");
          },
          tap: evt => {
            let idx =
              document.querySelector(".swiper-container").swiper.clickedIndex +
              1;
            this.keySelected(idx);
          }
        }
      },
      items: []
    };
  },
  created() {},
  mounted() {
    if (this.$store.getters.scanner > 0) this.scannerColor = "red";
    this.$root.$on("tryagain", function() {
      this.$router.replace({ name: "game" });
    });
    this.$root.$on("quit", function() {
      this.$router.go(-1);
    });

    this.playbeep(require("@/assets/fanfare.wav"));

    setTimeout(() => {
      this.bg = require("@/assets/minimg/castle.png");
      this.localStage = 1;
    }, 3000);
  },
  beforeDestroy() {},
  computed: {
    ...mapGetters(["athena", "scanner"]),
    guideMessage() {
      let message;
      switch (this.localStage) {
        case -1:
          message =
            "황금의 성에 진입했습니다. 남은 관문들을 돌파해서 황금을 차지하세요.";
          break;
        case 0:
          message =
            "잘못된 문을 열었습니다. 빨리 도망가서 다시 한번 도전해 보세요.";
          break;
        case 1:
          message =
            "보물상자가 숨겨진 방을 찾으세요. 황금탐지기 아이템의 도움을 한번 받으실 수 있습니다.";
          break;
        case 2:
          message =
            "숫자를 좌우로 돌려 비밀번호를 찾으세요. 아테나 아이템의 도움을 한번 받으실 수 있습니다.";
          break;
        case 3:
          message =
            "위 네개의 숫자 가운데 보물상자의 비밀번호가 있습니다. 잘 기억해 두세요.";
          break;
        case 4:
        case 5:
        case 6:
          message =
            "이제 다시 보물을 숨길 방을 선택해주세요. 보물을 숨기고 나면 게임을 끝낼 수 있습니다.";
          break;
        case 9:
          message =
            "비밀번호가 틀렸습니다. 괴수에게 당하기 전에 빨리 도망가서 다시 한번 도전해보세요.";
          break;
      }
      return message;
    }
  },
  methods: {
    selectDoor: function(num) {
      if (this.localStage === 1) {
        this.playbeep(require("@/assets/door.wav"));

        this.selectedDoor = num;
        switch (this.scannerStatus) {
          case 1:
          case 3:
            if (num === this.$store.getters.gemroom) {
              this.localStage = 2;
              this.scannerColor = "grey";
              if (this.$store.getters.athena > 0) this.athenaColor = "red";
              this.bg = require("@/assets/minimg/goldbox_off.png");
              this.selectedDoor = 0;
            } else {
              this.playbeep(require("@/assets/empty.mp3"));

              this.localStage = 6;
              this.scannerColor = "grey";
              this.bg = require("@/assets/minimg/empty.gif");
              // 종료 팝업 보여준 후 종료하거나 다시 게임으로 돌아간다.
              this.result = 0;
              setTimeout(() => {
                this.show = true;
              }, 1500);
            }
            break;
          case 2:
            this.$store.commit("setScanner", -1);
            this.scannerColor = "grey";
            this.scannerStatus = 3;
            if (num === this.$store.getters.gemroom) {
              this.bg = require("@/assets/minimg/goldbox_hint.png");
            } else {
              this.bg = require("@/assets/minimg/empty_hint.png");
            }
            break;
        }
      } else if (this.localStage === 5) {
        let newKey = Math.floor(Math.random() * (10 - 1 + 1)) + 1;
        this.$store.commit("setKey", newKey);
        this.$store.commit("setGemRoom", num);
        this.bg = require("@/assets/minimg/castle.png");
        this.localStage = 6;
        this.result = 1;
        this.show = true;
      }
    },
    useScanner: function() {
      if (this.scannerStatus === 1) this.scannerStatus = 2;
      else if (this.scannerStatus === 2) this.scannerStatus = 1;
    },
    useAthena: function() {
      this.$store.commit("setAthena", -1);
      this.athenaColor = "grey";
      this.athenaStatus = 2;

      this.localStage = 3;
      this.getHints(4);
      setTimeout(() => {
        this.localStage = 2;
      }, 2000);
    },
    getHints: function(numHints) {
      // 맵에 아이템이 숨어있나?
      let count = 0;
      let key = this.$store.getters.key;
      while (!this.items.includes(key) || !(this.items.length == 4)) {
        let newHint = Math.floor(Math.random() * (10 - 1 + 1)) + 1;
        if (this.items.includes(newHint)) continue;
        else {
          if (count >= numHints) {
            this.items[count % numHints] = newHint;
          } else {
            this.items.push(newHint);
          }
          count++;
        }
      }
    },
    keySelected: function(val) {
      console.log("key ", this.$store.getters.key, " and ", val);
      if (this.$store.getters.key === val) {
        this.playbeep(require("@/assets/fanfare.wav"));

        this.localStage = 4;
        this.bg = require("@/assets/minimg/goldbox.gif");
        this.athenaColor = "grey";
        setTimeout(() => {
          this.localStage = 5;
        }, 1500);
      } else {
        this.localStage = 9;
        this.scannerColor = "grey";
        this.bg = require("@/assets/minimg/empty.gif");
        this.result = 0;
        setTimeout(() => {
          this.show = true;
        }, 1500);
      }
    },
    showRanking() {
      this.rank = true;
    }
  }
};
</script>

<style lang="scss" scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

