  position: relative;
  overflow: hidden;
  height: 110px;
  padding: 0px;
}
.swiper-container {
  width: 110px !important;
  height: 110px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -55px;
  margin-top: -55px;
}
.swiper-slide {
  background-position: 50% !important;
  background-size: cover !important;
  width: 100% !important;

  &.slide-1 {
    background-image: url("../assets/minimg/num1.png");
  }
  &.slide-2 {
    background-image: url("../assets/minimg/num2.png");
  }
  &.slide-3 {
    background-image: url("../assets/minimg/num3.png");
  }
  &.slide-4 {
    background-image: url("../assets/minimg/num4.png");
  }
  &.slide-5 {
    background-image: url("../assets/minimg/num5.png");
  }
  &.slide-6 {
    background-image: url("../assets/minimg/num6.png");
  }
  &.slide-7 {
    background-image: url("../assets/minimg/num7.png");
  }
  &.slide-8 {
    background-image: url("../assets/minimg/num8.png");
  }
  &.slide-9 {
    background-image: url("../assets/minimg/num9.png");
  }
  &.slide-10 {
    background-image: url("../assets/minimg/num10.png");
  }
}
.bounce-enter-active {
  animation: bounce-in 1.5s;
}
.bounce-leave-active {
  animation: bounce-in 1.5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}
</style>
